* {
    box-sizing: border-box;
    border-spacing: 0px;
}

.tableContainer{
    width: 74.5%;
}

table{
    width: 100%;
    margin: 20px 0px;
    border-radius: 8px;
    font-size: 14px;
    /* border: 0.2px solid lightgray; */
    box-shadow: rgb(0 0 0 / 4%) 0px 0px 23px 0px;
}

tr{
    height: 55px;
    box-shadow: 0px 0px 1px rgb(0 0 0 / 18%);
}

tr:hover {
    background-color: rgba(230, 249, 255, 0.4);
}

.sortIcon{
    position: absolute;
}

.filter-wrapper {
    padding: 0 2px;
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.currency-dropdown{
    padding: 10px;
    width: 9.5%;
    border-radius: 6px;
    border: 0.2px solid lightgray;

}

select{
    border:none;
}

.pagination-div{
    display: inline-flex;
    margin: 0 20px;
}

.currency-select {
    border: none;
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: stretch;
    padding-top: 1px;
}

@media only screen and (max-width:780px) {
    .tableContainer{
        width: 100%;
    }
  }